import React, { useState } from 'react';
import './App.css';
import * as math from 'mathjs';



function App() {

  const [input, setInput] = useState('1+(8+9)/2+5')
  const [inputSubmitted, setInputSubmitted] = useState(false)
  const [expression, setExpression] = useState('')

  const textStyle = { color: 'white', fontSize: '20px', }

  function onChangeInput(e) { setInput(e.target.value) }
  function onClickInputBox(e) { e.target.select() }
  function handleOnSubmit(e) { 
    if (input!=='') {
      setExpression(math.parse(input)) 
      setInputSubmitted(true)
      setInput('')
    }
  }

  return (
    <>
      <p></p>
      <label style={textStyle}>Enter your expression:</label>
      <input className='input' type='text' value={input} onClick={onClickInputBox} onChange={onChangeInput}></input>
      <button className='submit' onClick={handleOnSubmit}>submit</button>

    </>
  )
}

